.Headerflex {
    display: flex;
    justify-content: space-around;
    background-color: black;
    padding: 10px 10px;
}


.Itemflex {
    display: flex;
    gap: 40px;
    list-style: none;
    color: white;
    margin-top: 8px;


}

.Headername {
    color: white;
    text-decoration: none;
    font-size: xx-large;
}

.Headername:hover {
    color: white;
}

.Itemflex li {
    position: relative;
 
}

.Itemflex li a {
    color: white;
    text-decoration: none;
}
.Itemflex li .active {
    color: #ffbd39;
}

.Itemflex li a:hover {
    color: #ffbd39;
    cursor: pointer;
}

.Itemflex li a:hover::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: -2px;
    left: 0;
    background: #ffbd39;
    transition: all 0.3s ease-in-out 0s;
}

.Itemflex li .active::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: -2px;
    left: 0;
    background: #ffbd39;
    transition: all 0.3s ease-in-out 0s;
}